@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@600&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefine Sans', serif;

$color-1: #DC1C13;
$color-2: #F07470;
$color-3: #EA4C46;
$color-4: #F1959B;
$color-5: #F6BDC0;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$min-height: 95vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;   
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font-1;
    color: $black;
}
button{
    cursor: pointer;
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: $black;
    cursor: pointer;
}
a:visited{
    color: $black;
}
.home, .knowledges, .portfolio, .contact{
    display: flex;

    @media screen and (max-width: 850px) {
        display: block !important;
    }
}
.homeContent, .knowledgesContent, .portfolioContent, .contactContent{
    margin-left: 0.4rem;

    @media screen and (max-width: 850px) {
        margin-left: 0 !important;
    }
}